<template>
	<div id="home">
		<div class="home-username">{{ message }}</div>
		<div class="home-infocenter">
			<span>{{ message1 }}</span>
			<span class="home-Newsnum">{{ message2 }}</span>
		</div>
		<div class="home-fun">
			<div class="home-fun-notes">
				<div class="home-fun-notes-icon home-fun-icon"></div>
				<div class="home-fun-font">{{ message3 }}</div>
			</div>
			<div class="home-fun-contacts">
				<div class="home-fun-contacts-icon home-fun-icon"></div>
				<div class="home-fun-font">{{ message4 }}</div>			
			</div>
			<div class="home-fun-business">
				<div class="home-fun-business-icon home-fun-icon"></div>
				<div class="home-fun-font">{{ message5 }}</div>				
			</div>
			<div class="home-fun-my">
				<div class="home-fun-my-icon home-fun-icon"></div>
				<div class="home-fun-font">{{ message6 }}</div>			
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data() {
			return{
				message  : " " ,
				message1 : "集团信息中心" ,
				message2 : "5" ,
				message3 : "便签" , 
				message4 : "通讯录" ,
				message5 : "企业圈" ,
				message6 : "我的"
			}
		},
		created() {
			this.Englishname();
		},
		methods:{
			Englishname(){
				const englishname = JSON.parse(localStorage.getItem('person'));
				this.message = englishname.Englishname
			}
		}
	}
</script>

<style>
	#home{
		position: relative;
		font-size: 3rem;
		color: #595758;
	}
	.home-username{
		position: absolute;
		width: 100%;
		top: -13rem;
		text-align: center;
		letter-spacing: .1rem;
		font: 3rem verdana;
		color: #fff;
	}
	.home-infocenter{
		position: relative;
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		width: 93%;
		height: 10rem;
		line-height: 10rem;
		margin: 6rem auto 2rem auto;
		border-radius: 1rem;
		border: .4rem solid red;
	}
	.home-infocenter span:first-child{
		font-size: 4rem;
		letter-spacing: .1rem;
	}
	.home-Newsnum{
		position: absolute;
		right: 6rem;
		height: 4rem;
		width: 4rem;
		line-height: 4rem;
		border-radius: 50%;
		background: red;
		color: #fff;
		font-size: 3.5rem;
	}
	.home-fun{
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: column;
		flex-direction: column;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		width: 90%;
		margin: 2rem auto;
		font-size: 3rem;
		border: 1px solid rgba(0,0,0,0);
	}
	.home-fun > div{
		position: relative;
		display: -webkit-flex;
		display: flex;
		-webkit-flex-direction: row;
		flex-direction: row;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		height: 10rem;
		border: 1px solid rgba(255,255,255,0);
	}
	.home-fun > div::after{
		position: absolute;
		content: '';
		top: 0;
		width: 100%;
		height: 100%;
		border-radius: 2rem;
		transform: scaleY(1.2) perspective(1000px) rotateX(-28deg);
		transform-origin: top;
		background: url(function-bg.svg) no-repeat 0/contain;
		filter: drop-shadow(0rem -0.5rem .5rem rgba(0,0,0,.5));
	}
	.home-fun > div:nth-of-type(4)::after{
		background: url(function-bg1.svg) no-repeat 0/contain;
	}	
	.home-fun-notes{
		width: 100%;
	}
	.home-fun-icon{
		width: 10rem;
		height: 4rem;
		z-index: 9;
	}
	.home-fun-notes-icon{
		background: url(notes-icon.svg) no-repeat right 0/contain;
	}
	.home-fun-font{
		margin-left: 10rem;
		height: 8rem;
		width: 12rem;
		text-align: left;
		line-height: 8rem;
		color: #595758;
		letter-spacing: .5rem;
		z-index: 9;
	}
	.home-fun-contacts{
		top: -2rem;
		width: 98%;
	}
	.home-fun-contacts-icon{
		background: url(contacts-icon.svg) no-repeat right 0/contain;
	}
	.home-fun-business{
		top: -4rem;
		width: 96%;
	}
	.home-fun-business-icon{
		background: url(business-icon.svg) no-repeat right 0/contain;
	}
	.home-fun-my{
		top: -6rem;
		width: 94%;
	}
	.home-fun-my-icon{
		background: url(my-icon.svg) no-repeat right 0/contain;
	}
</style>


