<template>
	<div id="login">
		<form action="">
			<div class="login-input-row">
				<input type="text" :placeholder="name" v-model="username" class="login-username login-input" />
			</div>
			<div class="login-input-row">
				<input type="password" :placeholder='pas' v-model="password" class="login-password login-input"/>
			</div>
			<div class="login-button-row">
				<input type="button" class="login-btn" @click="login"/>
			</div>
			<div class="login-forgetpas-row">
				<a href="#"> {{ fgpas }} </a>
			</div>
		</form>
	</div>
</template>

<script>
	export default{
		data () {
			return {
				name : "用户名",
				pas : "密	码" ,
				fgpas : "忘记密码" ,
				username :'' ,
				password :'' 
			}
		},
		methods:{
			login(){
				//this.$emit('login',this.username,this.password);
				this.$http.get('static/data.json').then(response => {
					this.result = response.body ;
					//console.log(this.result.person.length);
					if( this.result.person.username == hex_sha1(this.username) && this.result.person.password == hex_sha1(this.password)){
						console.log('验证通过')
						this.$emit('login',true);
						localStorage.setItem('person',JSON.stringify(this.result.person))
						var a = localStorage.getItem('person');
					}else{
						alert('验证失败')
					}
				},response => {
					console.log("error")
				})
			},
			change(){
				//console.log(this.username,this.password)
				this.$http.get('static/data.json').then(response => {
						this.result = response.body ;
						console.log(this.result.person);
						
					},response => {
						console.log("error")
					}
				)
			}
		}
	}
</script>

<style>
	#login{
		margin-top: 5rem;
	}
	::-webkit-input-placeholder{
		color: black;
	}	
	.login-input-row{
		width: 80%;
		height: 10rem;
		line-height: 10rem;
		margin: 0rem auto;
		border-bottom: 2px solid #ccc;
		background: #fff;
	}
	.login-input{
		width: 100%;
		height: 10rem;
		line-height: 10rem;
		padding-left: .5rem;
		border: none;	
		letter-spacing: .5rem;
		color: black;
		outline: none;
	}
	.login-password{
		letter-spacing: 2rem;
	}
	.login-button-row{
		display: -webkit-flex;
		display: flex;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		margin-top: 10rem;
		width: 100%;
		height: 15rem;
	}
	.login-btn{
		display: block;
		width: 15rem;
		height: 100%;
		padding: .4rem;
		border: .3rem solid #E60012;
		box-sizing: border-box;
		border-radius: 100%;
		outline: none;
		background: url(login-icon.png) no-repeat center 0/cover content-box;
	}
	.login-forgetpas-row{
		margin-top: 3rem;
		width: 100%;
		height: 3rem;
	}
	.login-forgetpas-row a{
		display: block;
		float: right;
		margin-right: 10%;
		letter-spacing: .2rem;
	}
	input:-webkit-autofill{
		-webkit-box-shadow:0 0 0 1000px white inset;
	}
</style>


