<template>
  <div id="all">
    <v-header></v-header>
    <v-login v-if='loginMark' @login = "loginApp"></v-login>
    <v-home v-if='homeMark'></v-home>
  </div>
</template>

<script>
	import header from "./components/header/header.vue"
	import login from "./components/login/login.vue"
	import home from '@/components/home/home.vue'
	export default {
		data(){
			return {
				loginMark : true ,
				homeMark : false
			}
		},
		components:{
			'v-header' : header , 
			'v-login' : login ,
			'v-home' : home
		},
		methods:{
			//参数为子组件传输过来的数据
			loginApp(msg){
				// this.loginMark = false ;
				// this.homeMark = true ;
				console.log(msg)
				if (msg === true){
					this.loginMark = false ;
					this.homeMark = true ;
				}
			}
		}
	}
</script>

<style>
	#all {
	  font-family: "microsoft yahei",'Avenir', Helvetica, Arial, sans-serif;
	  -webkit-font-smoothing: antialiased;
	  -moz-osx-font-smoothing: grayscale;
	  font-size: 2.8rem;
	  color: black;
	  text-align: center;
	}
</style>



